/** Media Queries **/
.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  text-align: center;
  font-size: 3.8rem;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .formulario .crear-proyecto {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario .crear-proyecto {
    width: auto;
  }
}
.dashboard .formulario .crear-proyecto:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.dashboard .formulario .guardar-perfil {
  background-color: rgb(0, 120, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario .guardar-perfil {
    width: auto;
  }
}
.dashboard .formulario .guardar-perfil:hover {
  background-color: #004500;
  cursor: pointer;
}

.sidebar {
  opacity: 0;
  height: 0;
  overflow: hidden;
  background: linear-gradient(to bottom, #2563EB 0%, #5484ef 100%);
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  height: 100%;
  padding: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, #2563EB 0%, #5484ef 100%);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
@media (min-width: 768px) {
  .sidebar {
    display: block;
    height: 100%;
    opacity: 1;
    padding: 2rem;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 6rem;
  width: 6rem;
  cursor: pointer;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  display: block;
  margin-bottom: 2rem;
  color: #FFFFFF;
  padding: 0.7rem;
  transition: all 300ms ease;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover {
  background-color: #0e3b9c;
  border-radius: 0.5rem;
}
.sidebar .sidebar-nav a.activo {
  background-color: #0e3b9c;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-session-mobile {
  margin-top: 10rem;
  border-radius: 0.5rem;
  background-color: #000;
  text-align: center;
  padding: 2rem;
}
.sidebar .cerrar-session-mobile .cerrar-session {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile {
    display: none;
  }
}
.sidebar.Proyectos {
  background: linear-gradient(to bottom, #0891B2 0%, #0ab9e3 100%);
}
.sidebar.Proyectos .sidebar-nav a {
  display: block;
  margin-bottom: 2rem;
  color: #FFFFFF;
