    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar.Perfil, .sidebar.Password {
  background: linear-gradient(to bottom, rgb(0, 120, 0) 0%, #00ab00 100%);
}
.sidebar.Perfil .sidebar-nav a, .sidebar.Password .sidebar-nav a {
  display: block;
