.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background: linear-gradient(to right, #0891B2 0%, #0ab9e3 100%);
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 600;
  transition: all 0.3s ease;
}
.listado-proyectos .proyecto:hover {
  background-color: #077d9a;
  transform: scale(1.05);
}
.listado-proyectos .proyecto a {
  color: #FFFFFF;
}

.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #134cca;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 0.2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 1.4rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 1rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completado {
  background-color: #2563EB;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  transform: translateY(-30rem);
  transition-property: transform, opacity;
  transition-duration: 0.5s;
  opacity: 0;
}
.modal .formulario legend {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario input[type=text] {
  border-radius: 0.5rem;
  background-color: #d8dadf;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #134cca;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.filtros {
  margin-top: 2rem;
  background-color: white;
  border-radius: 1rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filtros h2 {
  color: #2563EB;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .filtros h2 {
    margin: 0;
  }
}
.filtros .campo {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filtros .campo {
    margin-bottom: 0;
  }
}
.filtros input[type=radio] {
  accent-color: #2563EB;
  width: 1.6rem;
  cursor: pointer;
  height: 1.6rem;
}